.courseScan{
    height: 100%;
    font-family: 'Raleway', sans-serif;
    margin: 0;
    padding: 0;
}

.dark-courseScan *{
    color: rgba(255,255,255,.75);
}

.courseScan h2{
    background-image: linear-gradient(to right bottom, red,orange,yellow);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
}

.courseScan h4{
    color: grey;
}

.table{
    height: 100%;
    overflow: hidden;
}

.tbody{
    position: relative;
    overflow: auto;
    height: 100%;
}

.label div{
    font-size: .79em;
}

.tooltips{
    position: relative;
    background: rgba(90,162,249,0.7);
    border-top: 2px solid #5aa2f9;
    border-bottom: 2px solid #5aa2f9;
}

.tooltips div{
    height: 70%;
    width: 100%;
    border-right: 1px solid #5aa2f9;
}

.tooltips .tooltiptext {
    visibility: hidden;
    width: 175px;
    background-color: black;
    color: #fff;
    font-size: .9em;
    text-align: center;
    border-radius: 1em;
    z-index: 1;

    /* Position the tooltip */
    position: fixed;
}

/*.tooltips:hover .tooltiptext {
    visibility: visible;
}*/

.tooltips .tooltiptext:after {
    content: '';
    position: absolute;
    top: 100%;
    left: 10%;
    width: 0;
    height: 0;
    border-top: 8px solid #000000;
    border-right: 8px solid transparent;
    border-left: 8px solid transparent;
  }

/* width */
.tbody::-webkit-scrollbar {
    width: .5em;
}
  
  /* Track */
.tbody::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey; 
    border-radius: 1em;
}
   
  /* Handle */
.tbody::-webkit-scrollbar-thumb {
    background: rgba(150,150,150, .5); 
    border-radius: 1em;
}
  
  /* Handle on hover */
.tbody::-webkit-scrollbar-thumb:hover {
    background: rgba(150,150,150, 1); 
}

@media only screen and (max-width: 576px) {
    /* width */
    .tbody::-webkit-scrollbar {
        width: 0;
    }
}